:root {
  --primary-blue: #1e1e5c;
  --secondary-blue: #29489c;
  --primary-green: #28a745;
  --yellow: #e5db80;
  --light-blue: #e7f3ff;
  --dark-text: #343a3d;
  --background-color: var(--primary-blue);
  --border-color: #dee2e6;
  --card-background: #fff;
  --court-background: #0a0a57;
  --court-line-color: white;
  --gap: .75rem;
  --confirm-color: #28a745;
  --confirm-hover-color: #218838;
  --cancel-color: #dc3545;
  --cancel-hover-color: #c82333;
  --neutral-color: #6c757d;
  --neutral-hover-color: #5a6268;
  --inactive-color: #fb8f2c;
  --inactive-hover-color: #fca04a;
  --font-size-multiplier: 1;
  --display-size-multiplier: 1;
}

html {
  height: 100%;
}

body {
  background-color: var(--background-color);
  color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
  line-height: 1.6;
  font-size: calc(1rem * var(--font-size-multiplier));
  flex-direction: column;
  height: 100%;
  margin: 0;
  display: flex;
}

header {
  color: #fff;
  padding: var(--gap) var(--gap) calc(var(--gap) / 2);
  background-color: #0a0a57;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: center;
  display: flex;
  box-shadow: 0 2px 4px #0000001a;
}

header h1 {
  margin: 0;
}

#header-logo {
  width: auto;
  height: 70px;
  margin-top: -1rem;
  margin-bottom: -1rem;
  transform: translateY(5px);
}

.header-buttons {
  justify-content: flex-end;
  align-items: center;
  gap: 0;
  display: flex;
}

.mobile-menu-bar {
  background-color: var(--secondary-blue);
  z-index: 2000;
  height: 40px;
  padding: .5rem 0;
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0 -2px 10px #0000001a;
}

.mobile-menu-btn {
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  flex: 1;
  justify-content: center;
  align-items: center;
  padding: .5rem;
  font-size: 1.4rem;
  transition: background-color .2s;
  display: flex;
}

.mobile-menu-btn:active {
  background-color: #ffffff1a;
}

.mobile-menu-btn i {
  font-size: 1.4rem;
}

.container {
  padding: var(--gap);
  gap: var(--gap);
  flex-direction: column;
  flex-grow: 1;
  min-height: 0;
  display: flex;
}

.content-columns {
  gap: var(--gap);
  flex-grow: 1;
  grid-template-columns: repeat(4, 1fr);
  min-height: 0;
  display: grid;
}

.player-column {
  grid-column: span 1;
}

#availablePlayersSection {
  background-color: var(--card-background);
  color: var(--dark-text);
  border-radius: 8px;
  flex-direction: column;
  height: 0;
  display: flex;
  overflow: hidden;
}

#availablePlayersSection .card-body {
  flex-direction: column;
  flex: 1;
  min-height: 0;
  padding: 1rem;
  display: flex;
}

#availablePlayersSection h4 {
  font-size: calc(.8rem * var(--font-size-multiplier));
  text-transform: uppercase;
  color: var(--neutral-color);
  letter-spacing: .5px;
  margin: 0 0 .5rem;
  font-weight: 600;
}

#availablePlayersSection h4:nth-of-type(2) {
  margin-top: 1rem;
}

#courtsSection {
  grid-column: span 3;
}

.court-selection-overlay {
  z-index: 5;
  box-sizing: border-box;
  background-color: #0a0a57d9;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 100%;
  height: 100%;
  padding: 1rem;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.court-confirm-btn.select-court.reserved-ball {
  background-image: url("tennis-ball-reserved.8dca5024.png");
}

.court-confirm-btn.select-court {
  text-indent: 0;
  white-space: normal;
  text-align: center;
  color: #fff;
  width: 120px;
  height: 120px;
  font-weight: bold;
  font-size: calc(1.2rem * var(--font-size-multiplier));
  text-shadow: 2px 2px 4px #000, 0 0 10px #000c;
  -webkit-tap-highlight-color: transparent;
  background-color: #0000;
  background-image: url("tennis-ball.74d6ed71.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  line-height: 1.1;
  display: flex;
  flex: 0 0 120px !important;
  height: 120px !important;
  max-height: none !important;
  padding: 0 !important;
}

.court-confirm-btn.select-court:hover {
  opacity: 1;
  background-color: #ffffff1a;
  transform: scale(1.05);
}

@keyframes spinAndShrink {
  0% {
    opacity: 1;
    color: #fff;
    text-shadow: 2px 2px 4px #000, 0 0 10px #000c;
    transform: rotate(0);
  }

  10% {
    color: #0000;
    text-shadow: none;
  }

  100% {
    opacity: 0;
    color: #0000;
    text-shadow: none;
    transform: rotate(720deg);
  }
}

.court-confirm-btn.select-court.hide-anim {
  animation: 2s forwards spinAndShrink;
}

#time-overlay {
  top: var(--header-height);
  width: 100%;
  height: 75px;
  padding: 0 var(--gap);
  box-sizing: border-box;
  pointer-events: none;
  z-index: 1001;
  justify-content: space-between;
  align-items: center;
  transition: transform .3s ease-in-out, opacity .3s ease-in-out, visibility .3s ease-in-out;
  display: flex;
  position: fixed;
  left: 0;
  right: 0;
  transform: translateY(0);
}

#weather-display {
  color: #fff;
  font-size: calc(1.15rem * var(--font-size-multiplier));
  align-items: center;
  gap: .5rem;
  font-weight: 500;
  display: flex;
}

#icon-fader, #temp-fader {
  align-items: center;
  height: 28px;
  display: flex;
  position: relative;
}

#icon-fader {
  align-items: center;
  width: 40px;
  height: 28px;
  display: flex;
  position: relative;
}

#temp-fader {
  width: 60px;
  font-size: calc(1.15rem * var(--font-size-multiplier));
}

.weather-icon, #temp-fader span {
  opacity: 0;
  font-size: 1.8rem;
  transition: opacity .5s ease-in-out;
  position: absolute;
  left: 0;
}

.weather-icon.is-visible, #temp-fader span.is-visible {
  opacity: 1;
}

.temp-max {
  color: #ff6b6b;
}

.temp-min {
  color: #a9e1ff;
}

.temp-dew {
  color: #82e0aa;
}

#time-overlay > * {
  pointer-events: auto;
}

#left-spacer, #right-spacer {
  flex-shrink: 0;
}

.player-list-controls {
  border-bottom: 1px solid var(--border-color);
  gap: 1rem;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  display: flex;
}

.player-list-controls .action-btn {
  font-size: calc(.9rem * var(--font-size-multiplier));
  border: none;
  padding: .6rem .8rem;
  font-weight: bold;
}

.player-list-controls #checkInBtn {
  background-color: var(--confirm-color);
}

.player-list-controls #checkInBtn:hover {
  background-color: var(--confirm-hover-color);
}

.player-list-controls #checkOutBtn {
  background-color: var(--cancel-color);
}

.player-list-controls #checkOutBtn:hover {
  background-color: var(--cancel-hover-color);
}

#availablePlayersList {
  flex: 1;
  min-height: 0;
  margin: 0;
  padding: 0;
  list-style-type: none;
  overflow-y: auto;
}

#availablePlayersList li {
  box-sizing: border-box;
  border: 3px solid #0000;
  align-items: center;
  min-height: 54px;
  margin: 2px 0;
  padding: .4rem .8rem;
  line-height: 1.2;
  transition: background-color .2s;
  display: flex;
}

.player-stats {
  flex-shrink: 0;
  align-items: center;
  margin-left: auto;
  display: flex;
}

.player-stats > * {
  justify-content: center;
  align-items: center;
  display: flex;
}

.suggestion-icon-wrapper, .pause-toggle-btn {
  width: 35px;
}

.gender-container {
  width: 40px;
}

.player-playtime {
  justify-content: center;
  width: 65px;
  padding-right: .5rem;
}

#availablePlayersList li.first-player .player-playtime {
  padding-right: 1.1rem;
}

#availablePlayersList .next-players-group li.disabled {
  padding: .4rem .8rem;
}

.player-name-container {
  align-items: center;
  display: flex;
}

.player-name {
  color: var(--dark-text);
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: 500;
  font-size: calc(1rem * var(--font-size-multiplier));
  overflow: hidden;
}

.player-status {
  font-size: calc(.8rem * var(--font-size-multiplier));
  color: var(--neutral-color);
}

.player-priority {
  font-size: calc(.8rem * var(--font-size-multiplier));
  color: var(--inactive-color);
  font-weight: bold;
}

.player-stats {
  margin-left: var(--gap);
  flex-shrink: 0;
  align-items: center;
  gap: .5rem;
  display: flex;
}

.player-type-selector {
  border-top: 1px solid var(--border-color);
  justify-content: space-between;
  align-items: center;
  margin-top: 1.5rem;
  padding-top: 1.5rem;
  display: flex;
}

.player-type-selector .radio-group {
  background-color: #f0f0f0;
  border-radius: 8px;
  gap: 1rem;
  padding: .5rem;
  display: flex;
}

.gender-selector {
  justify-content: space-between;
  align-items: center;
  display: flex;
  margin-top: 0 !important;
  margin-bottom: .5rem !important;
}

#history-page .gender-selector .radio-group, .history-list .gender-selector .radio-group, .game-history-filter-container .gender-selector .radio-group {
  background-color: #0000 !important;
  border: none !important;
}

#history-page .modal-content, #manual-entry-modal .modal-content, #outside-player-modal .modal-content, #check-in-modal .modal-content, #check-out-modal .modal-content, #event-create-edit-modal .modal-content, #event-list-modal .modal-content {
  flex-direction: column;
  flex-grow: 1;
  height: 80vh;
  min-height: 0;
  display: flex;
  max-height: 80vh !important;
}

.gender-selector .radio-group {
  background-color: #f0f0f0;
  border-radius: 8px;
  gap: 1rem;
  padding: .5rem;
  display: flex;
}

#history-page .gender-selector .radio-group {
  background-color: #f0f0f0;
  border-radius: 8px;
  gap: 1rem;
  padding: .5rem 1rem;
  display: flex;
}

.gender-selector .radio-group, .player-type-selector .radio-group, .age-type-selector .radio-group {
  background-color: #f0f0f0;
  border-radius: 8px;
  gap: 1rem;
  padding: .5rem;
  display: flex;
}

.age-type-selector .radio-group {
  justify-content: space-around;
  min-width: 240px;
}

.gender-container {
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  display: flex;
}

.gender-container.gender-M {
  background-color: #007bff;
}

.gender-container.gender-F {
  background-color: #e83e8c;
}

.player-gender {
  color: #fff;
  font-weight: bold;
  line-height: 1;
  font-size: calc(1rem * var(--font-size-multiplier));
}

.player-playtime {
  font-size: calc(.9em * var(--font-size-multiplier));
  color: #6c757d;
  text-align: center;
  width: 55px;
}

#availablePlayersList li.waiting-message {
  color: #6c757d;
  cursor: default;
  font-style: italic;
  font-size: calc(1rem * var(--font-size-multiplier));
  justify-content: center;
  background-color: #0000 !important;
}

.player-details {
  text-align: left;
  flex: 2.5;
  min-width: 0;
}

.pause-toggle-btn, .gender-container, .player-playtime {
  flex: 1;
  justify-content: center;
  display: flex;
}

#availablePlayersList li:last-child {
  border-bottom: none;
}

#availablePlayersList li:hover {
  cursor: pointer;
  background-color: #f1f1f1;
}

.player-list-header {
  justify-content: space-between;
  align-items: baseline;
  margin-top: 1rem;
  padding: 0 .8rem 0 0;
  display: flex;
}

#availablePlayersSection.mode-selector-hidden .player-list-header {
  margin-top: .2rem;
}

.player-list-header h4 {
  text-align: left;
  flex: 2.5;
  margin: 0;
}

.player-list-sub-headers {
  flex: 3;
  align-items: center;
  display: flex;
}

.player-list-sub-headers span {
  font-size: calc(.8rem * var(--font-size-multiplier));
  text-transform: uppercase;
  color: var(--neutral-color);
  font-weight: 600;
}

.header-sex {
  text-align: center;
  text-align: center;
  flex: 1;
  width: 24px;
  padding-left: 4rem;
}

.header-time {
  text-align: center;
  flex: 1;
}

.summary-card {
  background-color: var(--card-background);
  color: var(--dark-text);
  box-sizing: border-box;
  border-radius: 8px;
  flex-direction: column;
  height: 100%;
  min-height: auto;
  display: flex;
  overflow: hidden;
}

.summary-card .card-body {
  flex-direction: column;
  flex: 1;
  justify-content: space-between;
  padding: 1rem;
  display: flex;
}

.settings-btn {
  font-size: calc(1.2rem * var(--font-size-multiplier));
  cursor: pointer;
  color: var(--neutral-color);
  background: none;
  border: none;
  padding: .5rem;
  transition: color .2s;
}

.settings-btn:hover {
  color: var(--primary-blue);
}

.on-duty-section {
  border-bottom: 1px solid #eee;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  padding-bottom: 1rem;
  display: flex;
}

.duty-info-container {
  align-items: center;
  gap: 1rem;
  display: flex;
}

#alert-status-display {
  flex-direction: column;
  align-items: flex-end;
  display: flex;
}

#alert-status-display .timer-value {
  font-size: calc(1.2rem * var(--font-size-multiplier));
  color: var(--dark-text);
  font-weight: 500;
}

#alert-status-display .timer-label {
  font-size: calc(.85rem * var(--font-size-multiplier));
  color: var(--neutral-color);
  text-align: right;
  width: 100%;
  font-weight: 400;
}

.duty-image-placeholder {
  border: 2px solid var(--border-color);
  color: #ccc;
  cursor: pointer;
  background-color: #f0f0f0;
  border-radius: 50%;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  font-size: 2rem;
  transition: background-color .2s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.duty-image-placeholder:hover {
  background-color: #e0e0e0;
}

.duty-icon, .duty-text {
  transition: opacity .5s ease-in-out;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.duty-icon {
  color: var(--confirm-color);
  font-size: 2.2rem;
  line-height: 1;
  animation: 10s ease-in-out infinite fadeCycle, 2s ease-in-out infinite scaleBounce;
}

.duty-text {
  font-size: calc(.9rem * var(--font-size-multiplier));
  color: var(--confirm-color);
  opacity: 0;
  font-weight: bold;
  animation: 10s ease-in-out infinite fadeCycleText;
}

.modal-overlay .modal-actions {
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1rem;
  padding-top: .5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  display: flex;
}

#ball-sale-type-modal .modal-actions button {
  margin-bottom: 0 !important;
}

.modal-actions {
  box-sizing: border-box;
  justify-content: center;
  gap: 1rem;
  width: 100%;
  margin-bottom: 1rem;
  padding-top: .5rem;
  display: flex;
}

.modal-actions button:only-child {
  flex-grow: 1;
  max-width: 100%;
}

.modal-grid-2x2 {
  grid-template-rows: 1fr 1fr;
  gap: 1rem;
  width: 100%;
  margin: 0;
  grid-template-columns: 1fr 1fr !important;
  display: grid !important;
}

.modal-grid-2x2 button {
  font-size: calc(1rem * var(--font-size-multiplier));
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: auto;
  padding: .5rem 1.5rem;
  font-weight: bold;
  line-height: 1.2;
  display: block;
  flex: 50% !important;
  margin: 0 !important;
}

#new-parent-modal .score-input-area input[type="text"], #new-parent-modal .score-input-area input[type="tel"], #guest-name-modal .score-input-area input[type="text"] {
  box-sizing: border-box;
  width: 100%;
  font-size: calc(1.1rem * var(--font-size-multiplier));
  border: 1px solid var(--border-color);
  text-align: left;
  border-radius: 5px;
  height: auto;
  padding: .8rem 1rem;
}

#new-parent-modal #remove-child-btn-main {
  color: var(--primary-blue) !important;
  border: 2px solid var(--primary-blue) !important;
  background-color: #fff !important;
}

@keyframes fadeCycle {
  0%, 35% {
    opacity: 1;
  }

  45%, 90% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeCycleText {
  0%, 45% {
    opacity: 0;
  }

  55%, 80% {
    opacity: 1;
  }

  90%, 100% {
    opacity: 0;
  }
}

@keyframes bounce {
  0%, 100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }
}

@keyframes scaleBounce {
  0%, 100% {
    transform: translate(-50%, -50%)scale(1);
  }

  50% {
    transform: translate(-50%, -50%)scale(1.2);
  }
}

.duty-info p {
  margin: 0;
}

.duty-name {
  font-size: calc(1.2rem * var(--font-size-multiplier));
  color: var(--dark-text);
  font-weight: 500;
}

.duty-label {
  font-size: calc(.85rem * var(--font-size-multiplier));
  color: var(--neutral-color);
  font-weight: 400;
}

.summary-stats {
