  display: flex;
}

.stats-column > div {
  justify-content: space-between;
  display: flex;
}

.summary-stats-grid strong {
  font-weight: bold;
}

.summary-stats-grid .available-value {
  color: var(--confirm-color);
}

.summary-stats-grid .on-court-value {
  color: var(--cancel-color);
}

.quote-section {
  color: #6c757d;
  text-align: center;
  width: 100%;
  height: 5em;
  font-style: italic;
  font-size: calc(1rem * var(--font-size-multiplier));
