}

.court-card.selectable .court-selection-overlay {
  background-color: #0a0a57d9;
}

.card-header {
  border-bottom: 1px solid var(--border-color);
  box-sizing: border-box;
  background-color: #f8f9fa;
  justify-content: space-between;
  align-items: center;
  gap: .5rem;
  height: 54px;
  padding: .5rem 1rem;
  display: flex;
