  border-radius: 3px;
  height: 6px;
  animation-duration: 4s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  position: absolute;
}

.pong-paddle:not(.double) {
  width: 12.5%;
  left: 35%;
}

.pong-paddle.double {
  width: 12.5%;
  transform: translateX(3.125%);
}

.pong-paddle.top-paddle {
  top: 17.5%;
}

.pong-paddle.bottom-paddle {
  bottom: 17.5%;
}

.pong-paddle:not(.double).top-paddle {
  animation-name: pong-top-move;
}

.pong-paddle:not(.double).bottom-paddle {
  animation-name: pong-bottom-move;
}

.pong-paddle.double.dl {
  left: 23%;
}

.pong-paddle.double.dr {
  left: 61.5%;
}

.pong-ball {
  opacity: .9;
  background-color: #0000;
  background-image: url("tennis-ball.74d6ed71.png");
  background-size: cover;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation-name: pong-ball-move;
  animation-duration: 4s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  position: absolute;
  transform: translate(-50%, -50%);
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes pong-top-move {
  0% {
    transform: translateX(-90%);
  }

  25% {
    transform: translateX(220%);
  }

  50% {
    transform: translateX(220%);
  }

  75% {
    transform: translateX(-90%);
  }

  100% {
    transform: translateX(-90%);
  }
}

@keyframes pong-bottom-move {
  0% {
    transform: translateX(220%);
  }

  25% {
    transform: translateX(220%);
  }

  50% {
    transform: translateX(-90%);
  }

  75% {
    transform: translateX(-90%);
  }

  100% {
    transform: translateX(220%);
  }
}

@keyframes pong-move-tr {
  0%, 20% {
    transform: translateY(-25px)translateX(3.125%);
  }

  25% {
    transform: translateY(0)translateX(3.125%);
  }

  55%, 100% {
    transform: translateY(-25px)translateX(3.125%);
  }
}

@keyframes pong-move-tl {
  0%, 70% {
    transform: translateY(-25px)translateX(3.125%);
  }

  75% {
    transform: translateY(0)translateX(3.125%);
  }

  20%, 100% {
    transform: translateY(-25px)translateX(3.125%);
  }
}

@keyframes pong-move-br {
  0% {
    transform: translateY(0)translateX(3.125%);
  }

  30%, 95% {
    transform: translateY(25px)translateX(3.125%);
  }

  100% {
    transform: translateY(0)translateX(3.125%);
  }
}

@keyframes pong-move-bl {
  0%, 45% {
    transform: translateY(25px)translateX(3.125%);
  }

  50% {
    transform: translateY(0)translateX(3.125%);
  }

  80%, 100% {
    transform: translateY(25px)translateX(3.125%);
  }
}

@keyframes pong-ball-move {
  0% {
    top: 79.5%;
    left: 68.75%;
    transform: translate(-50%, -50%)rotate(0);
  }

  25% {
    top: 20.5%;
    left: 68.75%;
    transform: translate(-50%, -50%)rotate(900deg);
  }

  50% {
    top: 79.5%;
    left: 31.25%;
    transform: translate(-50%, -50%)rotate(-900deg);
  }

  75% {
    top: 20.5%;
    left: 31.25%;
    transform: translate(-50%, -50%)rotate(900deg);
  }

  100% {
    top: 79.5%;
    left: 68.75%;
    transform: translate(-50%, -50%)rotate(-900deg);
  }
}

